<!-- 资源平台首页 -->
<template>
  <div class="res-index">
    <!-- 轮播 -->
    <el-carousel class="base-carousel" height="360px">
      <el-carousel-item>
        <div class="banner-1"></div>
      </el-carousel-item>
      <el-carousel-item>
        <div class="banner-2"></div>
      </el-carousel-item>
      <el-carousel-item>
        <div class="banner-3"></div>
      </el-carousel-item>
    </el-carousel>
    <!-- tips -->
    <div class="tips">
      <div class="tip-content">
        <resTip class="tip-1">
          <div class="item">
            <p>{{ visits |toThousand}}</p>
            <p>总计访问量</p>
          </div>
        </resTip>
        <resTip class="tip-2">
          <div class="item">
            <p>{{resourceCount | toThousand}}</p>
            <p>资源总数</p>
          </div>
        </resTip>
        <resTip class="tip-3">
          <div class="item">
            <p>+{{newAdded | toThousand}}</p>
            <p>本周新增</p>
          </div>
        </resTip>
        <resTip class="tip-4">
          <div class="item">
            <p>
              <i class="iconfont icon-upload-cloud"></i> 上传资源
            </p>
          </div>
        </resTip>
      </div>
    </div>
    <template v-for="item in resHomePageClassifyList">
      <div :class="[item.className,{oddBg:item.sort%2===0}]" :key="item.sort">
        <div :class="item.className + '-content'" class="content-center">
            <res-title :title="item.classifyName"></res-title>
            <!-- 精选专辑 -->
            <template v-if="item.resSpecialsList && item.classifyName==='精选专辑'">
              <res-box show :info="specialList" v-for="(specialList,i) in item.resSpecialsList" :key="i"></res-box>
            </template>
            <!-- 最新资源 -->
            <template v-if="item.resResourceList && item.classifyName==='最新资源'">
              <res-box :info="resourceList" v-for="(resourceList,i) in item.resResourceList" :key="i"></res-box>
            </template>
            <!-- 热门资源 -->
            <template v-if="item.resResourceList && item.classifyName==='热门资源'">
              <res-box :info="resourceList" v-for="(resourceList,i) in item.resResourceList" :key="i"></res-box>
            </template>
            <!-- 同步课堂 -->
            <template v-if="item.resResourceList && item.classifyName==='同步课堂'">
              <res-box :info="resourceList" v-for="(resourceList,i) in item.resResourceList" :key="i"></res-box>
            </template>
            <!-- 精品推荐 -->
            <template v-if="item.resResourceList && item.classifyName==='精品推荐'">
              <res-box :info="resourceList" v-for="(resourceList,i) in item.resResourceList" :key="i"></res-box>
            </template>
        </div>
      </div>
    </template>
    <res-footer></res-footer>
  </div>
</template>

<script>
export default {
  data(){
    return {
      id:0,
      visits:0,
      resourceCount:0,
      newAdded:0,
      resHomePageClassifyList:[],
    }
  },
  components: {
    resTip: () => import("@/components/res-tip"),
    resTitle: () => import("@/components/res-title"),
    resBox: () => import("@/components/res-box"),
    resFooter: () => import("@/components/res-footer")
  },
  created(){
    this.id = this.$route.query.id || 0;
    this.initHomePage();
    this.recordVisit();
  },
  mounted() {
    // console.log(document.body.clientWidth);
  },

  methods:{
    recordVisit(){
      this.$api.recordVisit()
    },
    initHomePage() {
      this.$api.homeSelectData(this.id).then(res => {
        const {visits,resourceCount,newAdded,resHomePageClassifyList} = res.data;
        this.visits = visits;
        this.resourceCount = resourceCount;
        this.newAdded = newAdded;
        this.resHomePageClassifyList = this.addclassAndSort(resHomePageClassifyList);
      })
    },
    addclassAndSort(arr){
      const temp = arr.map(item => {
        let className = '';
        switch (item.classifyName) {
          case "热门资源":
            className = "popular";
            break;
          case "精品推荐":
            className = "recommend";
            break;
          case "最新资源":
            className = "latest";
            break;
          case "同步课堂":
            className = "sync";
            break;
          case "精选专辑":
            className = "album";
            break;
          default:
            className=""
        }
        return Object.assign(item,{className})
      })
      return temp.sort(function(a,b){
        return a.sort - b.sort
      })
    },
  },
  filters: {
    /* 格式化数字 */
    toThousand(num) {
      return (num || 0).toString().replace(/(\d)(?=(?:\d{3})+$)/g, "$1,");
    }
  }
};
</script>

<style lang="scss" scoped="scoped">
.content-center {
  width: 81.6%;
  margin: 0 auto;
}

.res-index {
  min-width: 1200px;
  .base-carousel {
    .banner-1 {
      width: 100%;
      height: 100%;
      background: url(./../../../assets/image/banner-test-1.jpg) no-repeat;
      background-size: 100% 100%;
    }

    .banner-2 {
      width: 100%;
      height: 100%;
      background: url(./../../../assets/image/banner-test-2.jpg) no-repeat;
      background-size: 100% 100%;
    }

    .banner-3 {
      width: 100%;
      height: 100%;
      background: url(./../../../assets/image/banner-test-3.jpg) no-repeat;
      background-size: 100% 100%;
    }
  }

  .tips {
    .tip-content {
      width: 82%;
      margin: 30px auto;
      display: flex;
      justify-content: space-between;

      & > div {
        width: 23%;
      }

      .item {
        p {
          color: #fff;

          &:first-child {
            font-size: 28px;
            font-weight: bold;
          }

          &:last-child {
            font-size: 16px;
          }
        }
      }

      .tip-1 {
        background: url(./../../../assets/image/res-index-1.png) no-repeat;
        background-size: 100% 100%;
      }

      .tip-2 {
        background: url(./../../../assets/image/res-index-2.png) no-repeat;
        background-size: 100% 100%;
      }

      .tip-3 {
        background: url(./../../../assets/image/res-index-3.png) no-repeat;
        background-size: 100% 100%;
      }

      .tip-4 {
        background: #409fff;
        .item p,
        i {
          font-size: 20px;
        }
        i {
          color: #fff;
        }
      }
    }
  }

  .recommend {
    padding-bottom: 30px;
    .recommend-content {
        display: flex;
        flex-wrap: wrap;
        & > div:not(:first-child) {
          width: 25%;
        }
        @media screen and (min-width: 1520px) {
          & > div:not(:first-child) {
            width: 20%;
          }
        }
    }
  }
  .latest {
    padding-bottom: 30px;
    .latest-content {
      display: flex;
      flex-wrap: wrap;
      & > div:not(:first-child) {
        width: 25%;
      }
      @media screen and (min-width: 1520px) {
        & > div:not(:first-child) {
          width: 20%;
        }
      }
    }
  }

  .popular {
    padding-bottom: 30px;
    .popular-content {
      display: flex;
      flex-wrap: wrap;
      & > div:not(:first-child) {
        width: 25%;
      }
      @media screen and (min-width: 1520px) {
        & > div:not(:first-child) {
          width: 20%;
        }
      }
    }
  }
  .album {
    padding-bottom: 30px;
    .album-content {
      display: flex;
      flex-wrap: wrap;
      & > div:not(:first-child) {
        width: 25%;
      }
    }
  }

  .sync {
    padding-bottom: 30px;
    .sync-content {
      display: flex;
      flex-wrap: wrap;
      & > div:not(:first-child) {
        width: 25%;
      }
      @media screen and (min-width: 1520px) {
        & > div:not(:first-child) {
          width: 20%;
        }
      }
    }
  }
  .oddBg {
    background: #f7f8fb;
  }
}
</style>
